import { Component, OnInit } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { DataserviceService } from 'app/dataservice.service';

declare var bibtexParse: any;

@Component({
  selector: 'app-publications',
  templateUrl: './publications.component.html',
  styleUrls: ['./publications.component.css'],
  providers: [DataserviceService]
})
export class PublicationsComponent implements OnInit {

  private bibList: Array<any>;
  searchText: string;

  constructor(private dataservice: DataserviceService) { }


  ngOnInit() {
    this.dataservice.getBibList().subscribe(response => {
      response.forEach(element => {
        element.bibtex = this.toBibtex(element);
      });
      this.bibList = response;
    });
  }

  // tslint:disable:forin
  toBibtex = function (json) {
    let out = '';
    out += '@' + json.entryType;
    out += '{';
    if (json.citationKey) {
      out += json.citationKey + ', ' + '\n\t';
    }
    if (json.entry) {
      out += json.entry + '\n';
    }
    if (json.entryTags) {
      let tags = '';
      for (const jdx in json.entryTags) {
        if (tags.length !== 0) {
          tags += ', '  + '\n\t';

        }
        tags += jdx + '= {' + json.entryTags[jdx] + '}';
      }
      out += tags;
    }
    out += '\n';
    out += '}\n\n';
    // console.log(out)
    return out;
  };

  // toBibtex(item) {
  //   console.log(item, bibtexParse.toBibtex(item));
  //   return bibtexParse.toBibtex(item);
  // }
}



