import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { PublicationsComponent } from './publications/publications.component';
import { PortfolioComponent } from './portfolio/portfolio.component';
import { AboutmeComponent } from './aboutme/aboutme.component';
import { DissertationComponent } from './dissertation/dissertation.component';
import { ResearchquestionsComponent } from './researchquestions/researchquestions.component';
import { NewsComponent } from './news/news.component';
import { BannerComponent } from './banner/banner.component';
import { ContactComponent } from './contact/contact.component';
import { FooterComponent } from './footer/footer.component';
import { MaincontentComponent } from './maincontent/maincontent.component';

import 'rxjs/Rx';
import { HttpModule } from '@angular/http';
import { RemovecharPipe } from './removechar.pipe';
import { LatexPipe } from './latex.pipe';
import { SupervisionComponent } from './supervision/supervision.component';
import { TeachingComponent } from 'app/teaching/teaching.component';
import { CounterComponent } from './counter/counter.component';
import { ProjectComponent } from './project/project.component';

import { ClipboardModule } from 'ngx-clipboard';
import { FilterPipe } from './filter.pipe';
import { FormsModule } from '@angular/forms';
import { HighlightPipe } from './highlight.pipe';

const appRoutes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full'},
  { path: 'home', component: MaincontentComponent },
  { path: 'publications', component: PublicationsComponent },
  { path: 'supervision', component: SupervisionComponent },
  { path: 'teaching', component: TeachingComponent },
  { path: 'projects', component: ProjectComponent },
  // {
  //   path: '',
  //   redirectTo: '/home',
  //   pathMatch: 'full'
  // },
  {
    path: '**',
    redirectTo: 'home',
    pathMatch: 'full'
  }
];

@NgModule({
  declarations: [
    AppComponent,
    PublicationsComponent,
    PortfolioComponent,
    AboutmeComponent,
    DissertationComponent,
    ResearchquestionsComponent,
    NewsComponent,
    BannerComponent,
    ContactComponent,
    FooterComponent,
    MaincontentComponent,
    RemovecharPipe,
    LatexPipe,
    SupervisionComponent,
    TeachingComponent,
    CounterComponent,
    ProjectComponent,
    FilterPipe,
    HighlightPipe
  ],
  imports: [
    BrowserModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FormsModule,
    ClipboardModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



