import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'latex'
})
export class LatexPipe implements PipeTransform {

//   Ä (hold down the Alt key and type 0196)
// ä (Alt 0228)
// Ö (Alt 0214)
// ö (Alt 0246)
// Ü (Alt 0220)
// ü (Alt 0252)
// ß (Alt 0223)
  transform(value: string, args?: any): string {
    value = value.replace('{\\"{A}}', 'Ä');
    value = value.replace('\\"{A}', 'Ä');


    value = value.replace('{\\"{a}', 'ä');
    value = value.replace('\\"{a}', 'ä');
    value = value.replace('�', 'ä');
    value = value.replace('\\"a', 'ä');

    value = value.replace('\\"{O}', 'Ö');
    value = value.replace('{\\"{O}}', 'Ö');
    value = value.replace('{\\"{o}}', 'ö');
    value = value.replace('\\"{o}', 'ö');
    value = value.replace('�', 'ö');
    value = value.replace('\\"o', 'ö');

    value = value.replace('\\"{U}', 'Ü');
    value = value.replace('{\\"{U}}', 'Ü');
    value = value.replace('{\\"{u}}', 'ü');
    value = value.replace('\\"{u}', 'ü');
    value = value.replace('\\"u', 'ü');

    value = value.replace('{\\\'e}', 'é');

    return value;
  }

}



