import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from "rxjs/Rx";

declare var bibtexParse: any;

@Injectable() 
export class DataserviceService {

  constructor(private http: Http) {
  }


  public getNewsList(): Observable<Array<any>>{
    let list$ = this.http
      .get('assets/news.json')
      .map((response: Response) => response.json());
    return list$;

  }

  public getTeachingList(): Observable<Array<any>>{
    let list$ = this.http
      .get('assets/teaching.json')
      .map((response: Response) => response.json());
    return list$;

  }

  public getNumberOfTeachings(): Observable<number> {
    let list$ = this.getTeachingList()
      .map(response => response.length)
    return list$;
  }  
  
  public getSupervisionList(): Observable<Array<any>>{
    let list$ = this.http
      .get('assets/supervision.json')
      .map((response: Response) => response.json());
    return list$;

  }

  public getNumberOfTheses(): Observable<number> {
    let list$ = this.getSupervisionList()
      .map(response => response.length)
    return list$;
  }

  public getProjectList(): Observable<Array<any>>{
    let list$ = this.http
      .get('assets/projects.json')
      .map((response: Response) => response.json());
    return list$;

  }

  public getNumberOfProjects(): Observable<number> {
    let list$ = this.getProjectList()
      .map(response => response.length)
    return list$;
  }

  public getBibList(): Observable<Array<any>> {
    let bibList$ = this.http
      .get('assets/Publications.bib')
      .map(this.extractData);
    return bibList$;

    // this.http.get('assets/Publications.bib')
    //   .map((response: Response) => response.json())
    //   .subscribe(
    //     data => this.extractData(data),
    //     err => this.handleError(err)
    // );

    // return this.bibList;
  }


  public getNumberOfBibList(): Observable<number> {
    let list = this.getBibList()
      .map(bibList => bibList.length);
    return list;
  }


  private extractData(res: Response): Array<any>{
    let csvData = res['_body'] || '';
    let bibList = bibtexParse.toJSON(csvData);
    bibList.sort((item1, item2) => {
      if (item1.entryTags.year > item2.entryTags.year) {
        return -1;
      } else if (item1.entryTags.year < item2.entryTags.year) {
        return 1;
      }
      return 0;
    });
    return bibList;
  }

  private handleError(error: any) {
    // In a real world app, we might use a remote logging infrastructure
    // We'd also dig deeper into the error to get a better message
    let errMsg = (error.message) ? error.message :
      error.status ? `${error.status} - ${error.statusText}` : 'Server error';
    console.error(errMsg); // log to console instead
    return errMsg;
  }

}



