import { Component, OnInit } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { DataserviceService } from "app/dataservice.service";

@Component({
  selector: 'app-supervision',
  templateUrl: './supervision.component.html',
  styleUrls: ['./supervision.component.css'],
  providers: [DataserviceService]
})
export class SupervisionComponent implements OnInit {

  private supervisionList: Array<any>;

  private clicked: any;

  constructor(private dataservice: DataserviceService) {
    this.dataservice.getSupervisionList().subscribe((list) => {
      list.sort((item1, item2) => {
        if (item1.start > item2.start) {
          return -1;
        } else if (item1.start < item2.start) {
          return 1;
        }
        return 0;
      });
      this.supervisionList = list;
      this.clicked = list[0]; //just for init purposes is exchanged later
    });
  }

  ngOnInit() {

  }

  itemClicked(item: any) {
    this.clicked = item;
  }

}



